import {_decorator, Component, instantiate, MeshRenderer, Node, Prefab, Size} from 'cc';

const {ccclass, property} = _decorator;

@ccclass('Game_Board')
export class Game_Board extends Component {
  @property(Node) origin: Node;
  @property(Prefab) board_region_prefab: Prefab;

  public show_grids(size: Size) {
    let origin_pos = this.origin.position;
    console.log();

    for (let x = 0; x < size.width; x++) {
      for (let y = 0; y < size.height; y++) {
        let region: Node = instantiate(this.board_region_prefab);
        region.setParent(this.node);
      }
    }
  }

  public showGrid() {}
}
